.fab-button {
    z-index: 1000;
    top: 85vh;
    left: 90vw;
}
@media (max-width: 720px){
    .fab-button {
        z-index: 1000;
        top: 85vh;
        left: 80vw;
    }
}
.fab-container {
    position: fixed;
    display: flex;
    flex-direction: row;
    z-index: 1000;
}
.super-container-light {
    background-color: #f5f5f5;
}
.super-container-dark {
    background-color: #212529;
}
.super-container-light .dim {
    color: rgba(0, 0, 0, 0.452);
}
.super-container-dark .dim {
    color: rgba(255, 255, 255, 0.5);
}
.super-container-dark hr {
    border-color: #f8f9fa;
}