/* section 1 */
.section1-light .profile {
    background-color: #e6e6e6;
}
.name {
    font-size: 3rem !important;
}
.greeting {
    font-size: 4rem !important;
}
@media (max-width: 750px) {
    .name {
        font-size: 2.5rem !important;
    }
}
.items-skill {
    font-weight: 600;
}

.section1-dark .profile {
    background-color: #343A40;
}
.section1-dark {
    color: #F8F9FA;
}
.section1-dark .link {
    color: #F8F9FA;
}