/* section 3 */
.educationDetails {
    display: flex;
    align-items: center;
  }
  @media (max-width: 750px) {
    .educationDetails {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
    }
  }
  .educationDetails .item {
    display: flex;
    align-items: center;
  }
  .rotated {
    writing-mode: tb-rl;
    rotate: -180deg;
  }
  .year1 {
    background-color: #565ad3ad;
    color: white;
    height: 9rem;
  }
  .year2 {
    background-color: #565ad3d5;
    color: white;
    height: 9rem;
  }
  .year3 {
    background-color: #565ad3;
    color: white;
    height: 9rem;
  }

.edu-dark {
    color: #F8F9FA;
}
.edu-dark .year1 {
    background-color: #ff333377;
    color: white;
    height: 9rem;
}
.edu-dark .year2 {
    background-color: #ff3333c0;
    color: white;
    height: 9rem;
}
.edu-dark .year3 {
    background-color: #ff3333;
    color: white;
    height: 9rem;
}