/* section 2 */
.nav-light {
    background-color: white;
}

.nav-dark {
    background-color: #111316;
    color: #f8f9fa;
}
.nav-dark .nav-link {
    color: #f8f9fa;
}
.nav{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    flex-direction: row;
}
.nav a{
    background-color: #E9ECEF;
    width: 12rem;
    text-align: center;
}

.nav-dark a{
    background-color: #343A40;
    width: 12rem;
    text-align: center;
}
@media (max-width: 720px) {
    .nav{
        flex-direction: column;
    }
}
